'use client';

import { useEffect, useState, use } from 'react';
import dynamic from 'next/dynamic';
import Link from 'next/link';
import { motion } from 'framer-motion';
import {
    MapPin,
    Calendar,
    Target,
    ChevronLeft,
    Clock,
    Share2,
    Download,
    RefreshCw,
    ExternalLink,
    Trophy,
    TrendingUp,
    AlertCircle
} from 'lucide-react';
import { Card, Button, Badge, Progress } from '@/components/ui';

// Dynamically import Map component to avoid SSR issues with Leaflet
const MapComponent = dynamic(() => import('@/components/ui/Map'), {
    ssr: false,
    loading: () => (
        <div className="h-full w-full flex items-center justify-center bg-zinc-900/50">
            <RefreshCw className="w-8 h-8 text-zinc-600 animate-spin" />
        </div>
    ),
});

interface Result {
    id: string;
    lat: number;
    lng: number;
    rank: number | null;
}

interface Scan {
    id: string;
    keyword: string;
    status: string;
    gridSize: number;
    radius: number;
    frequency: string;
    createdAt: string;
    centerLat: number;
    centerLng: number;
    results: Result[];
}

export default function ScanReportPage({ params }: { params: Promise<{ id: string }> }) {
    const resolvedParams = use(params);
    const [scan, setScan] = useState<Scan | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchScan = () => {
            fetch(`/api/scans/${resolvedParams.id}`)
                .then(res => res.json())
                .then(data => setScan(data.scan))
                .catch(console.error)
                .finally(() => setLoading(false));
        };

        fetchScan();

        const interval = setInterval(() => {
            if (scan?.status === 'RUNNING' || scan?.status === 'PENDING') {
                fetchScan();
            }
        }, 5000);

        return () => clearInterval(interval);
    }, [resolvedParams.id, scan?.status]);

    if (loading) {
        return (
            <div className="p-8 flex items-center justify-center min-h-screen">
                <div className="text-center">
                    <RefreshCw className="w-8 h-8 text-blue-500 animate-spin mx-auto mb-4" />
                    <p className="text-zinc-400">Loading scan data...</p>
                </div>
            </div>
        );
    }

    if (!scan) {
        return (
            <div className="p-8 flex items-center justify-center min-h-screen">
                <Card className="text-center py-12 px-8">
                    <AlertCircle className="w-12 h-12 text-red-400 mx-auto mb-4" />
                    <h2 className="text-xl font-bold text-white mb-2">Scan Not Found</h2>
                    <Button variant="outline" onClick={() => window.location.href = '/scans'}>Back to Scans</Button>
                </Card>
            </div>
        );
    }

    const totalPoints = scan.gridSize * scan.gridSize;
    const completedPoints = scan.results.length;
    const progress = Math.round((completedPoints / totalPoints) * 100);

    const top3Count = scan.results.filter(r => r.rank && r.rank <= 3).length;
    const top10Count = scan.results.filter(r => r.rank && r.rank > 3 && r.rank <= 10).length;
    const beyondCount = scan.results.filter(r => r.rank && r.rank > 10).length;
    const notFoundCount = scan.results.filter(r => !r.rank).length;

    // Generate grid points if results are empty (for visualization pending scan)
    // This is complex because we need lat/lng relative to center.
    // For now, only show map if we have at least one result or just the center.
    const mapPoints = scan.results.length > 0 ? scan.results : [];

    return (
        <div className="p-8 max-w-[1600px] mx-auto">
            {/* Header */}
            <motion.header
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                className="mb-8"
            >
                <Link
                    href="/scans"
                    className="inline-flex items-center gap-2 text-zinc-400 hover:text-white transition-colors mb-6 group"
                >
                    <ChevronLeft className="w-4 h-4 group-hover:-translate-x-1 transition-transform" />
                    Back to Scans
                </Link>

                <div className="flex flex-col lg:flex-row justify-between items-start lg:items-end gap-6">
                    <div>
                        <div className="flex items-center gap-4 mb-2">
                            <h1 className="text-4xl font-bold text-white">"{scan.keyword}"</h1>
                            <Badge
                                variant={scan.status === 'COMPLETED' ? 'success' : 'primary'}
                                dot={scan.status === 'RUNNING'}
                            >
                                {scan.status}
                            </Badge>
                        </div>
                        <div className="flex flex-wrap gap-6 text-sm text-zinc-400">
                            <span className="flex items-center gap-2">
                                <MapPin className="w-4 h-4" />
                                {scan.centerLat.toFixed(4)}, {scan.centerLng.toFixed(4)}
                            </span>
                            <span className="flex items-center gap-2">
                                <Target className="w-4 h-4" />
                                {scan.gridSize}×{scan.gridSize} Grid ({scan.radius}km)
                            </span>
                        </div>
                    </div>

                    <div className="flex items-center gap-3">
                        <Button variant="outline"><Share2 className="w-4 h-4" /> Share</Button>
                        <Button variant="outline"><Download className="w-4 h-4" /> Export</Button>
                    </div>
                </div>
            </motion.header>

            <div className="grid grid-cols-1 xl:grid-cols-3 gap-8">
                {/* Map Visualization */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.1 }}
                    className="xl:col-span-2"
                >
                    <Card className="h-[600px] p-0 overflow-hidden border-0 relative">
                        <MapComponent
                            center={[scan.centerLat, scan.centerLng]}
                            zoom={11} // Adjustable based on radius, simplified for now
                            points={mapPoints}
                            gridSize={scan.gridSize}
                        />
                    </Card>
                </motion.div>

                {/* Stats Panel */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.2 }}
                    className="space-y-6"
                >
                    <Card>
                        <h3 className="text-lg font-bold text-white mb-6 flex items-center gap-2">
                            <Trophy className="w-5 h-5 text-amber-400" />
                            Rank Distribution
                        </h3>
                        <div className="space-y-5">
                            <div className="space-y-2">
                                <div className="flex justify-between text-sm">
                                    <span className="text-zinc-400">Top 3</span>
                                    <span className="text-emerald-400 font-bold">{top3Count}</span>
                                </div>
                                <Progress value={top3Count} max={completedPoints || 1} size="sm" />
                            </div>
                            <div className="space-y-2">
                                <div className="flex justify-between text-sm">
                                    <span className="text-zinc-400">4 - 10</span>
                                    <span className="text-amber-400 font-bold">{top10Count}</span>
                                </div>
                                <div className="w-full bg-white/10 rounded-full h-1">
                                    <div className="h-1 bg-amber-500 rounded-full" style={{ width: `${(top10Count / (completedPoints || 1)) * 100}%` }} />
                                </div>
                            </div>
                            <div className="space-y-2">
                                <div className="flex justify-between text-sm">
                                    <span className="text-zinc-400">10+</span>
                                    <span className="text-red-400 font-bold">{beyondCount}</span>
                                </div>
                                <div className="w-full bg-white/10 rounded-full h-1">
                                    <div className="h-1 bg-red-500 rounded-full" style={{ width: `${(beyondCount / (completedPoints || 1)) * 100}%` }} />
                                </div>
                            </div>
                        </div>
                    </Card>

                    <Card className="bg-gradient-to-br from-emerald-900/20 to-emerald-900/5 border-emerald-500/20">
                        <div className="flex items-center gap-4">
                            <div className="w-16 h-16 rounded-2xl bg-emerald-500/20 flex items-center justify-center">
                                <TrendingUp className="w-8 h-8 text-emerald-400" />
                            </div>
                            <div>
                                <p className="text-sm text-emerald-400/80">Visibility Score</p>
                                <p className="text-4xl font-bold text-white">
                                    {completedPoints > 0
                                        ? Math.round((top3Count / completedPoints) * 100)
                                        : 0}%
                                </p>
                            </div>
                        </div>
                    </Card>
                </motion.div>
            </div>
        </div>
    );
}
